<template>
    <v-layout row wrap>
        <v-container fluid>
            <v-flex xs12>
                <v-card>
                    <v-card-title>                  
                        <v-toolbar-title>RPA Logging History</v-toolbar-title>
                        <v-spacer></v-spacer>
                        <v-flex xs12 sm2 style="margin-right:15px;">
                            <v-select
                                :items="bots"
                                v-model="selectedBots"
                                item-text="bot_id"
                                item-value="bot_id"
                                label="Select Bots"
                                multiple
                                @change="changeBots"
                            >
                                <template v-slot:selection="{ item, index }">
                                    <v-chip v-if="index === 0">
                                        <span>{{ item.bot_id }}</span>
                                    </v-chip>
                                    <v-chip v-if="index === 1">
                                        <span>{{ item.bot_id }}</span>
                                    </v-chip>
                                    <span
                                    v-if="index === 2"
                                    class="grey--text caption"
                                    >(+{{ selectedBots.length - 2 }} others)</span>
                                </template>
                            
                            </v-select>
                        </v-flex>
                    
                        <v-flex xs12 sm3 style="margin-right:20px;">
                            <v-select
                                :items="jobs"
                                v-model="jobsSelected"
                                item-text="job_name"
                                item-value="job_id"
                                label="Select Jobs"
                                multiple
                                @change="changeJobs"
                            >
                                <template v-slot:selection="{ item, index }">
                                    <v-chip v-if="index === 0">
                                        <span>{{ item.job_name }}</span>
                                    </v-chip>
                                    <span
                                    v-if="index === 1"
                                    class="grey--text caption"
                                    >(+{{ jobsSelected.length - 1 }} others)</span>
                                </template>    
                            </v-select>
                        </v-flex>

                        <v-flex xs12 sm1>
                            <v-menu
                            v-model="datePicker_1"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            lazy
                            transition="scale-transition"
                            offset-y
                            full-width
                            min-width="290px"
                            >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                    v-model="startdate"
                                    label="Start Date"
                                    prepend-icon="event"
                                    readonly
                                    v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker v-model="startdate" @input="datePicker_1 = false" @change="changeStartDate"></v-date-picker>
                            </v-menu>
                        </v-flex>
                        
                        <div style="margin-right:6px;">~</div>
                       
                        <v-flex xs12 sm1>
                            <v-menu
                            v-model="datePicker_2"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            lazy
                            transition="scale-transition"
                            offset-y
                            full-width
                            min-width="290px"
                            >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                    v-model="enddate"
                                    label="End Date"
                                    prepend-icon="event"
                                    readonly
                                    v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker v-model="enddate" @input="datePicker_2 = false" @change="changeEndDate"></v-date-picker>
                            </v-menu>
                        </v-flex>

                    </v-card-title>
                    <v-data-table
                    :headers="headers"
                    :items="gridData_History"
                    >
                        <template v-slot:items="props">
                            <td>{{ props.item.bot_id }}</td>
                            <td>{{ props.item.bot.bot_name }}</td>
                            <td>{{ props.item.job_id }}</td>
                            <td>{{ props.item.job.job_name }}</td>
                            <td>{{ props.item.step_id }}</td>
                            <td>{{ props.item.step.step_name }}</td>
                            <td>{{ props.item.status.code_name }}</td>
                            <td>{{ props.item.run_date }}</td>
                            <td>{{ props.item.elapsedTime }}</td>
                            <td>{{ props.item.description }}</td>
                        </template>
                        <template v-slot:no-gridData_History>
                            <v-alert :value="true" color="error" icon="warning">
                            Your search for "{{ search }}" found no gridData_History.
                            </v-alert>
                        </template>
                    </v-data-table>
                </v-card>
            </v-flex>
        </v-container>
    </v-layout>
</template>

<script>
const moment = require('moment')
var botList = "";
var jobList = "";
var startDate = moment().subtract(30, 'days').format('YYYY-MM-DD');
var endDate = moment().format('YYYY-MM-DD');

export default {
    data() {
        return {
            startdate: startDate,
            enddate: endDate,
            datePicker_1: false,
            datePicker_2: false,
            headers: [
                { text: 'Bot ID', value: 'bot_id', sortable: false },
                { text: 'Bot Name', value: 'bot_name' },
                { text: 'Job ID', value: 'job_id' },
                { text: 'Job Name', value: 'job_name' },
                { text: 'Step ID', value: 'step_id' },
                { text: 'Step Name', value: 'step_name' },
                { text: 'Status', value: 'code_name' },
                { text: 'Run Date', value: 'run_date' },
                { text: 'Elapsed Time(Minute)', value: 'elapsedTime' },
                { text: 'Description', value: 'description' }
            ],
            pagination: {rowsPerPage: 10},
            gridData_History: [],
            bots: [],
            selectedBots: [],
            jobs: [],
            jobsSelected: [],
        }
    },
    mounted() {
         // bot 셀렉트 박스
        this.axios.get(this.$store.state.rOpsApi+'/bots').then(response => {
            this.bots = response.data
            this.selectedBots = response.data

            for (var i in this.selectedBots) {
                if (i==0) botList = this.selectedBots[i].bot_id
                else botList += "," + this.selectedBots[i].bot_id 
            }

            // job 셀렉트 박스
            this.axios.get(this.$store.state.rOpsApi+'/jobs/selectbox').then(response => {
                this.jobs = response.data
                this.jobsSelected = response.data

                for (var i in this.jobsSelected) {
                    if (i==0) jobList = this.jobsSelected[i].job_id
                    else jobList += "," + this.jobsSelected[i].job_id 
                }
                 // RPA Logging History 테이블
                this.axios.get(this.$store.state.rOpsApi+'/logging?bots=' + botList + '&jobs=' + jobList + '&startDate=' + startDate + '&endDate=' + endDate).then(response => {
                    this.gridData_History = response.data
                })
            })
        })
        
    },
    methods: {
        changeBots(slBotIdList) {
            botList = slBotIdList;

            this.axios.get(this.$store.state.rOpsApi+'/logging?bots=' + botList + '&jobs=' + jobList + '&startDate=' + startDate + '&endDate=' + endDate).then(response => {
                this.gridData_History = response.data
            })
        },
        changeJobs(slJobIdList) {
            jobList = slJobIdList;

            this.axios.get(this.$store.state.rOpsApi+'/logging?bots=' + botList + '&jobs=' + jobList + '&startDate=' + startDate + '&endDate=' + endDate).then(response => {
                this.gridData_History = response.data
            })
        },
        changeStartDate(date) {
            startDate = date;

            this.axios.get(this.$store.state.rOpsApi+'/logging?bots=' + botList + '&jobs=' + jobList + '&startDate=' + startDate + '&endDate=' + endDate).then(response => {
                this.gridData_History = response.data
            })
        },
        changeEndDate(date) {
            endDate = date;

            this.axios.get(this.$store.state.rOpsApi+'/logging?bots=' + botList + '&jobs=' + jobList + '&startDate=' + startDate + '&endDate=' + endDate).then(response => {
                this.gridData_History = response.data
            })
        }
    }
}
</script>

